@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
.main-container {
  height: 90vh;
  width: 100vw;
  display: flex;
  transition: 1s ease;
}

.information-main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 2vmax;
  text-shadow: 5px 5px 3px rgba(0, 0, 0, 0.2);
  font-weight: 700;
  height: 85%;
  width: 60%;
}

.information-main-container h1 {
  margin-left: 6vw;
}

.name {
  color: #2FC18B;
}

.image-main-container {
  display: grid;
  place-items: center;
  height: 100%;
  width: 40%;
}

.image-main-container img {
  width: 85%;
}

@keyframes caret {
  0% {
    border-color: transparent;
  } 
  50% {
    border-color: white;
  }
}

.palavras {
  border-right: 5px solid white;
  animation: caret 3s infinite ease;
  text-shadow: 5px 5px 3px rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 420px) {
  .main-container {
    flex-direction: column-reverse;
  }

  .image-main-container {
    width: 100%;
  }

  .information-main-container {
    width: 100%;
  }
}

.information-about-container {
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.information {
  width: 70%;
  margin-bottom: 10vh;
}

.information h1 {
  font-size: 3vmax;
  margin-bottom: 15px;
  text-align: center;
  text-decoration: underline;
}

.information p {
  font-weight: 700;
  font-size: 1.2vmax;
  text-align: justify;
}

@media screen and (max-width: 420px) {
  .information-about-container {
    width: 100%;
  }

  .information h1 {
    font-size: 5vmax;
  }

  .information p {
    font-size: 1.5vmax;
  }
}
.contact-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-container a {
  color: unset;
  background-color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  display: grid;
  place-items: center;
  width: 250px;
  height: 250px;
  border: 3px solid black;
  border-radius: 30px;
  box-shadow: 10px 10px rgba(0, 0, 0, 0.2);
  transition: 1s;
}

.contact-container a:hover {
  background-color: white;
}

.contact-container a img {
  width: 90%;
  border-radius: 30px;
}

