.projects-container {
  height: 90vh;
  width: 100vw;
  padding: 50px;
  display: flex;
  flex-wrap: wrap;
}

.pasta-projetos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 200px;
  width: 200px;
  margin-right: 20px;
  font-size: 0.7vmax;
  border-radius: 30px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.pasta-projetos:active {
  background-color: rgba(255, 255, 255, 0.4);
}

.pasta-projetos h2 {
  text-align: center;
  width: 70%;
}

.pasta-projetos svg {
  font-size: 6vmax;
}

.pasta-aberta {
  top: 15%;
  left: 15%;
  height: 75vh;
  width: 70vw;
  border-radius: 15px;
  overflow: hidden;
  position: absolute;
  animation: aparecer 1s ease;
  background-color: #232323;
}

@keyframes aparecer {
  0% {
    left: 5%;
    opacity: 0;
  }
  100% {
    left: 15%;
    opacity: 1;
  }
}

.pasta-header {
  display: flex;
  flex-direction: row;
  justify-content: end;
  background-color: #161616;
  width: 100%;
  height: 8%;
}

.folder-name {
  display: grid;
  place-items: center;
  width: 80%;
  height: 100%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.pasta-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
}

.projeto {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  color: unset;
  text-decoration: none;
  width: 150px;
  height: 125px;
  padding: 10px;
  border-radius: 10px;
  transition: .5s ease;
  margin-right: 15px;
}

.projeto:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.projeto img {
  width: 100%;
}

.projeto h3 {
  text-align: center;
}

.icons {
  margin-right: 10px;
  height: 100%;
  width: 8%;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.icons svg {
  cursor: pointer;
}

@media screen and (max-width: 420px) {
  .pasta-projetos svg {
    font-size: 8vmax;
  }

  .projects-container {
    padding: 0;
  }

  .pasta-projetos h2 {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }

  .pasta-aberta {
    width: 90vw;
  }

  .pasta-header {
    justify-content: space-between;

  }

  .icons {
    width: 25%;
  }

  .folder-name {
    width: 65%;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
}

body, #root {
  background-image: url(https://images.pexels.com/photos/4737484/pexels-photo-4737484.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
  color: rgba(255, 255, 255, 0.856);
  width: 100vw;
  overflow: hidden;
}
.header {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.1);
  width: 100vw;
  height: 10vh;
}

.menu {
  display: none;
  position: absolute;
  font-size: 3em;
  z-index: 10;
  top: 1%;
  right: 4%;
}

@keyframes showMenu {
  0% {
    left: 100%;
  }
  100% {
    left: 0%;
  }
}

.active {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  top: 0;
  left: 0;
  animation: showMenu 1s ease;
  z-index: 5;
}

.active nav {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.active nav a {
  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
  color: white;
  text-shadow: 0 0 1em black;
  text-decoration: none;
  transition: .5s;
}

.header nav {
  height: 100%;
  width: 40%;
  display: flex;
}

.header nav a {
  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
  color: white;
  text-decoration: none;
  transition: .5s;
}

.header nav a:hover {
  color: black;
  background-color: white
}

@media screen and (max-width: 420px) {
  .header nav {
    display: none;
  }

  .menu {
    display: block;
  }
}
